.searchBox {
    height: 40px;
    font-size: 20px;
    font-weight: 500;
    font-family: "Roboto", "Arial", sans-serif;
}

.searchBox:focus {
    outline: none !important;
    border: 0.5px solid blue;
    box-shadow: inset 0 0 1.5px 1.5px #888;
}

.searchIcon {
    height: 50px;
    padding: 7px 20px;
    border: 1px solid grey;
    background-color: #f2f8fa;
    cursor: pointer;
}

.micIcon {
    background-color: #f2f8fa;
    border: 1px solid #f2f8fa;
    padding: 2px;
    margin: 0px 5px;
    border-radius: 50%;
    cursor: pointer;
}

.searchField {
    display: flex;
    align-items: center;
}

@media only screen and (min-width : 850px) {
    .searchBox {
        width: 450px;
    }
}

@media only screen and (max-width : 600px) {
    .searchBox {
        width: 150px;
    }
}

.searchBox::placeholder {
    color: rgb(0, 0, 0, 0.5);
    font-size: 20px;
}

.searchBox::-webkit-search-cancel-button {
    height: 20px;
    width: 20px;
    cursor: pointer;
}